@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-image:url(/Looper/static/media/background.89d8943b.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}









.on{

    background-color:rgb(219, 171, 109);

}

.off{
    background-color:rgba(165, 42, 42, 0.5);
}
.square{
    width: 150px;
    height: 150px;
    font-size: 16px;
    color: white;
    border: white 3px solid;
    border-radius: 10px;
}
@media only screen and (max-width: 768px) {
    .square{
        width: 100px;
        height: 100px;
        font-size: 12px;
        color: white;
        border: white 3px solid;
        border-radius: 10px;
        z-index: 11;
    }  
}
.container{
    display:grid;
    grid-column-gap:10px;
    grid-row-gap:10px;
    grid-template-columns: auto auto auto;
    width:100%;

}
.container-item{
    width: 100px;
    height: 100px;
}
body {
    background-color: #333;
  }
  
  .play-btn {
      display: block;
      position: relative;
      width: 50px;
      height: 50px;
      text-indent: 100%;
      overflow: hidden;
      border: 4px solid #fff;
      border-radius: 100%;
      transition: background-color .3s;
  }
  .img-rope{
      position: absolute;
      background-image: url(/Looper/static/media/rope2.2570a858.png);
      background-size: 100%;
      background-repeat: no-repeat;
      height: 500px;
      top: 0;
      left: 250px;
      width: 190px;
      z-index: 10;
  }
  .play-btn:before,
  .play-btn:after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
  }
  
  .play-btn:before {
      width: 0;
      height: 0;
      margin-left: 3px;
      border-color: transparent;
      border-left-color: #fff;
      border-width: 10px 0 10px 14px;
      border-style: solid;
  }
  
  .play-btn:hover:not(.is-loading) {
      background-color: #fff;
  }
  
  .play-btn:hover:before {
      border-left-color: #333;
  }
  
  .is-playing:before,
  .is-playing:after {
      width: 5px;
      height: 18px;
      background-color: #fff;
  }
  
  .is-playing:before {
      border: none;
      margin-left: -5px;
  }
  
  .is-playing:after {
      margin-left: 5px;
  }
  
  .is-playing:hover:before,
  .is-playing:hover:after {
      background-color: #333;
  }
  
  .is-loading:before {
      display: none;
  }
  
  .is-loading:after {
      width: 20px;
      height: 20px;
      background-color: #fff;
      border-radius: 100%;
      -webkit-animation: .5s infinite alternate loading;
              animation: .5s infinite alternate loading;
  }
  
  @-webkit-keyframes loading {
      0% {width: 0; height: 0;}
      100% {width: 100%; height: 100%;}
  }
  
  @keyframes loading {
      0% {width: 0; height: 0;}
      100% {width: 100%; height: 100%;}
  }

  @media only screen and (max-width: 768px) {

    .img-rope{
        position: absolute;
        background-image: url(/Looper/static/media/ropemobile.359c22ba.png);
        background-size: 100%;
        background-repeat: no-repeat;
        height: 800px;
        -webkit-transform: rotate(-14deg);
                transform: rotate(-14deg);

        top: 165px;
        left: -35px;
        z-index: 10;
    }
  }
#start-record-btn{
    border: none;
    color: white;
    background-color: transparent;
    font-size: 50px;
    margin-left: 30px;

}
@-webkit-keyframes record {
  
    100%  {background-size: 120% ; }



  }
@keyframes record {
  
    100%  {background-size: 120% ; }



  }
.record-btn{
    display: flex;
    position: absolute;
    top: 235px;
    margin-left: 65px;
}
.record-video{
    position: absolute;
    left: -730px;
    top: 132px;
    border: #ffffff 5px solid;
    border-radius: 10px;
    width: 350px;
    height: 350px;
}
#stop-record-btn{
    animation: record 1s backwards infinite reverse;
    border: 5px white solid;
    display: block;
    padding: 5px;
    width: 40px;
    height: 40px;
    padding-bottom: 0px;
    display: block;
background-color: transparent;
    border-radius: 100px;
    color: #e04040;
    background: url('https://api.iconify.design/entypo/controller-record.svg?color=%23e04040') no-repeat center center / contain;
}
#stop-record-btn-noanim{
    border: 5px white solid;
    display: block;
    padding: 5px;
    width: 40px;
    height: 40px;
    padding-bottom: 0px;
    display: block;
background-color: transparent;
    border-radius: 100px;
    color: #e04040;
    background: url('https://api.iconify.design/entypo/controller-record.svg?color=%23e04040') no-repeat center center / contain;
}
#pause-record-btn{
    color: white;
    font-size: 40px;
    border: none;
    background-color: transparent;
}
#resume-record-btn{
    color: green;
    border: none;
    background-color: transparent;
    font-size: 40px;

}
@media only screen and (max-width: 768px) {
    .record-btn{
        display: flex;
        position: absolute;
        top: 140px;
        margin-left: 50px;
    }
    .record-video{
        position: absolute;
        left: -245px;
        top: 509px;
        border: #ffffff 5px solid;
        border-radius: 10px;
        width: 300px;
        height: 300px;
    }
}
.title{

    font-family: 'Monoton', cursive;
    font-size: 7rem;
    color:ghostwhite;
    margin-bottom: 0%;
    margin-top: 0%;
}
@-webkit-keyframes loop {
  
    0%  { color:ghostwhite ; }
    12.5%   { color:rgb(245, 94, 94) ; }
    25%   { color:rgb(242, 152, 79) ; }
    37.5%   { color:rgb(247, 214, 67) ; }
    50%   { color:rgb(193, 247, 67) ; }
    62.5% { color:rgb(51, 244, 199) ; }
    75%  { color:rgb(61, 126, 237) ; }
    87.5%  { color:rgb(169, 61, 245) ; }
    100% { color:rgb(210, 49, 242) ; }


  }
@keyframes loop {
  
    0%  { color:ghostwhite ; }
    12.5%   { color:rgb(245, 94, 94) ; }
    25%   { color:rgb(242, 152, 79) ; }
    37.5%   { color:rgb(247, 214, 67) ; }
    50%   { color:rgb(193, 247, 67) ; }
    62.5% { color:rgb(51, 244, 199) ; }
    75%  { color:rgb(61, 126, 237) ; }
    87.5%  { color:rgb(169, 61, 245) ; }
    100% { color:rgb(210, 49, 242) ; }


  }
.title-play{

    font-family: 'Monoton', cursive;
    font-size: 7rem;
    color:ghostwhite;
    margin-bottom: 0%;
    margin-top: 0%;
    -webkit-animation: loop 8s linear infinite;
            animation: loop 8s linear infinite;

}

@media only screen and (max-width: 768px) {

    .title{

        font-family: 'Monoton', cursive;
        font-size: 4rem;
        color:ghostwhite;
        margin-bottom: 0%;
        margin-top: 0%;
    } 
    .title-play{

        font-family: 'Monoton', cursive;
        font-size: 4rem;
        color:ghostwhite;
        margin-bottom: 0%;
        margin-top: 0%;
        -webkit-animation: loop 8s linear infinite;
                animation: loop 8s linear infinite;
    
    } 
}

